<template>
    <div class="main">
        <div class="goods-box">
            <div class="more-list">
                <div class="more-list-data" v-for="(item, index) in shopList" :key="index">
                    <div>
                        <img class="goods-img" :src="item.img" alt="">
                    </div>
                    <div class="commdity-item-name">
                        {{ item.text }}
                    </div>
                    <div class="commdity-jiage">
                        <!-- <span class="commdity-jiagename">零售价</span> -->
                        <span class="jiage">￥{{ item.price }}</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>

export default {
    data() {
        return {
            shopList: [
                {
                    text: 'iphone苹果12promax 256内存蓝色，国行双卡双待，纯原装无拆修，随意升级还原系统，无锁无暗病。',
                    price: '1299',
                    img: require('@/assets/home/commdity/iphone1.png')
                },
                {
                    text: 'iphone 14 promax 银白色 256g 国行 屏幕完美，在保365天！几本全新 原包装盒',
                    price: '1899',
                    img: require('@/assets/home/commdity/iphone2.png')
                },
                {
                    text: '美版无锁iPhone12，全网通，内存：128。颜色：黑白。随意升级、随意还原系统抹机',
                    price: '1399',
                    img: require('@/assets/home/commdity/iphone3.png')
                },
                {
                    text: 'iphone 14 promax 暗紫色 512g 国行 屏幕完美，在保365天！几本全新 原包装盒，原装充电器',
                    price: '2399',
                    img: require('@/assets/home/commdity/iphone4.png')
                },
                {
                    text: 'iPhone XR 128G 国行全网通无锁无ID，屏幕细微划痕',
                    price: '999',
                    img: require('@/assets/home/commdity/iphone5.png')
                },
                {
                    text: '苹果11国行iPhone11 128G 绿色，功能全好，面容正常，插卡即用',
                    price: '799',
                    img: require('@/assets/home/commdity/iphone6.png')
                }
            ],

        }
    },
    methods: {

    }

}
</script>
<style scoped lang="less">
.main {
    height: 100%;
    width: 100%;
}

.goods-box {
    padding-top: 50px;
    width: 1200px;
    margin: 0 auto;
    .more-list {
        display: flex;
        flex-wrap: wrap;

        .more-list-data:hover img {
            transform: scale(1.1);
        }

        .more-list-data:hover {
            box-shadow: 0 0 6px 2px #b3bad6;
            color: #527fda;
        }

        .more-list-data {
            cursor: pointer;
            margin: 10px 10px;
            width: 240px;
            background-color: #fff;
            border-radius: 10px;
            padding: 20px;
            height: 300px;

            img {
                transition: transform 0.3s ease;
                width: 220px;
                height: 210px;
                margin: 10px;
                border-radius: 10px;
            }

            .commdity-item-name {
                margin: 10px 0;
                overflow: hidden;
                text-overflow: ellipsis;
                height: 32px;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                font-size: 12px;

            }

            .commdity-jiage {
                font-size: 14px;
                margin-top: 10px;
                text-align: center;

                .commdity-jiagename {
                    color: #FF4300;
                }

                .jiage {
                    color: #FF4300;
                    font-weight: 600;
                    text-align: left;
                }
            }
        }
    }
}
</style>



